body {
  height: 3317px;
  background-image: url(../images/bg/donuts-mob.png), url(../images/bg/bg1-mob.png),
    url(../images/bg/bg2-mob.png), url(../images/bg/flover-mob.png);
  background-repeat: no-repeat;
  background-size: 117px 113px, 100% 416px, 100% 1214px, 87px 64px;
  background-position: left top, left top, center bottom 216px, right bottom;
  background-color: #fffff9;

  @media screen and (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/bg/donuts-mob2x.png), url(../images/bg/bg1-mob.png),
      url(../images/bg/bg2-mob.png), url(../images/bg/flover-mob2x.png);
  }

  @media screen and (min-width: 768px) {
    height: 2465px;
    background-image: url(../images/bg/donuts-tab.png), url(../images/bg/bg1-tab.png),
      url(../images/bg/flover-tab.png), url(../images/bg/bg2-tab.png);
    background-repeat: no-repeat;
    background-size: 361px 378px, 100% 622px, 147px 144px, 100% 1260px;
    background-position: right 137px top, left top, right bottom, center bottom 63px;
    background-color: #fffff9;

    @media screen and (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/bg/donuts-tab2x.png), url(../images/bg/bg1-tab.png),
        url(../images/bg/flover-tab2x.png), url(../images/bg/bg2-tab.png);
    }
  }

  @media screen and (min-width: 1280px) {
    height: 3171px;
    background-image: url(../images/bg/spoon.png), url(../images/bg/donuts-desk.png),
      url(../images/bg/bg1-desk.png), url(../images/bg/flover-desk.png),
      url(../images/bg/bg2-desk.png);
    background-repeat: no-repeat;
    background-size: 142px 450px, 719px 600px, 100% 1035px, 638px 368px, 100% 1599px;
    background-position: left top 166px, right top, left top, right bottom, center bottom;
    background-color: #fffff9;

    @media screen and (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/bg/spoon2x.png), url(../images/bg/donuts-desk2x.png),
        url(../images/bg/bg1-desk.png), url(../images/bg/flover-desk2x.png),
        url(../images/bg/bg2-desk.png);
    }
  }
}
